.card12 {
  position: relative;
  height: 350px;
  width: 300px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
  background-color: #2196F3;
  color: #fff;
  overflow: hidden;
  user-select: none;
}
@media only screen and (min-width: 410px) {
  .card12 {
    width: 350px;
  }
}
.card12 .card-body {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  height: 100%;
  width: 100%;
}
.card12 .card-body .card-content {
  position: absolute;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column nowrap;
  height: 100%;
  width: 100%;
  background-color: transparent;
  transition: width .3s, height .3s,transform .3s,opacity .2s;
  cursor: pointer;
}
.card12 .card-body .card-content:not(.suite-show) {
  opacity: 0;
}
.card12 .card-body .card-content .card-content-header, .card12 .card-body .card-content .card-content-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  background-color: #fff;
}
.card12 .card-body .card-content .card-content-header {
  background-color: #2196F3;
}
.card12 .card-body .card-content .card-content-footer a {
  position: absolute;
  text-decoration: none;
  padding: 8px;
  background-color: #01579B;
  color: #fff;
  font-size: 14px;
  transition: background .2s;
}
.card12 .card-body .card-content .card-content-footer a:hover {
  background-color: #0277BD;
}
.card12 .card-body .card-content .card-content-body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1 auto;
  text-align: center;
  font-size: 20px;
  line-height: 30px;
}
.card12 .card-body .card-content .card-content-body p {
  padding: 20px;
}
.card12 .card-body .card-content-lg {
  background-color: #f5f5f5;
  z-index: 5;
}
.card12 .card-body .card-content-lg .card-content-body {
  color: #333;
}
.card12 .card-body .card-content-lg.suite-min {
  z-index: 2;
  transform: translateY(10%) translateX(20%);
  width: 200px;
  height: 200px;
  border-radius: 50%;
}
.card12 .card-body .card-content-lg.suite-min .card-content-lg-header {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  color: #333;
  background-color: #f5f5f5;
}
.card12 .card-body .card-content-lg.suite-min .card-content-footer, .card12 .card-body .card-content-lg.suite-min .card-content-body {
  display: none;
}
.card12 .card-body .card-content-md {
  z-index: 5;
  background-color: #00695C;
}
.card12 .card-body .card-content-md.suite-min {
  z-index: 3;
  transform: translateY(60%) translateX(-40%);
  width: 140px;
  height: 140px;
  border-radius: 50%;
}
.card12 .card-body .card-content-md.suite-min .card-content-md-header {
  background-color: #00695C;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.card12 .card-body .card-content-md.suite-min .card-content-footer, .card12 .card-body .card-content-md.suite-min .card-content-body {
  display: none;
}
.card12 .card-body .card-content-sm {
  z-index: 5;
  background-color: #D84315;
}
.card12 .card-body .card-content-sm.suite-min {
  z-index: 4;
  transform: translateY(190%) translateX(10%);
  width: 100px;
  height: 100px;
  border-radius: 50%;
}
.card12 .card-body .card-content-sm.suite-min .card-content-sm-header {
  background-color: #D84315;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.card12 .card-body .card-content-sm.suite-min .card-content-footer, .card12 .card-body .card-content-sm.suite-min .card-content-body {
  display: none;
}
